@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap');

body {
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
  background-color: #fcfcfc;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}

.navbar {
  height: 60px;
  background-color: #a9def9;
  position: relative;
}

.logo {
}

.menuicon {
  display: none;
}

.navelements {
}

.navelements ul {
  display: flex;
  justify-content: space-between;
  list-style-type: none;
}

.navelements ul li:not(:last-child) {
  margin-right: 60px;
}

.navelements ul a {
  font-size: 16px;
  font-weight: 400;
  color: #2f234f;
  text-decoration: none;
}

.navelements ul a.active {
  color: #574c4c;
  font-weight: 500;
  position: relative;
}

.navelements ul a.active::after {
  content: '';
  position: absolute;
  bottom: -4px;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #574c4c;
}

@media screen and (max-width: 768px) {
  .navelements ul li:not(:last-child) {
    margin-right: 30px;
  }
}

@media screen and (max-width: 850px) {
  .menuicon {
    display: block;
    cursor: pointer;
  }

  .navelements {
    position: absolute;
    right: 0;
    top: 60px;
    background-color: #fef7e5;
    width: 0px;
    height: calc(100vh - 60px);
    transition: all 0.3s ease-in;
    overflow: hidden;
  }

  .navelements.active {
    width: 270px;
  }

  .navelements ul {
    display: flex;
    flex-direction: column;
  }

  .navelements ul li {
    margin-right: unset;
    margin-top: 22px;
  }
}